        <v-col cols="2" > <v-text-field label="()" :rules="[rules.required]" v-model="emaildata.template_params.name" type="text"></v-text-field> </v-col>
        <v-col cols="2" >  </v-col>
        <v-col cols="2"  class="fill-height"> <v-text-field label="/" :rules="[rules.required]" v-model="emaildata.template_params.company" type="text"></v-text-field> </v-col>
