<script>
import{ init } from 'emailjs-com';
import axios from 'axios'
init("user_yIyNcFF8djC4j2KFeDSit");
export default {
  name: "Third",
  data:()=>({
    checkBtn:false,
    rules:{
      required: value => !!value || ' .',
      email: value => {
        const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
        return pattern.test(value) || '  .'
      }
    },
    emaildata:{
      service_id:'service_nzg8z9i',
      template_id:'template_5s4cvmn',
      user_id:'user_yIyNcFF8djC4j2KFeDSit',
      template_params: {
        company: '',
        phone: '',
        name: '',
        email: '',
        context:''
      }
    },
    dialog: false
  }),
  methods:{
    checkAgree(){
      this.checkBtn=true
      this.dialog=false
    },
    onClose(){
      this.dialog=false;
      console.log('Close...')
      console.log(this.dialog)
    },
    viewPolicy(){
      this.dialog=true;
    },
    validCheck(){
      if(this.emaildata.template_params.email=='') {
        alert('     .')
        //this.failMsg='     .'
        return false;
      }
      else if(this.emaildata.template_params.name==''){
        alert('    ')
        return false;
      }else if(this.emaildata.template_params.context==''){
        alert('   !')
        return false;
      }else if(this.checkBtn==false){
        alert('     .')
        return false;
      }
      return true;
    },
    sendEmail(){
      if(!this.validCheck()){
        return
      }
      axios({
        method:'POST',
        url:'https://api.emailjs.com/api/v1.0/email/send',
        data:JSON.stringify(this.emaildata),
        headers:{
          "Content-Type":'application/json'
        }
      }).then(result=>{
        console.log(result+'Success')
        alert('  .  .')
      }).catch(error=>{
        console.log(error)
        alert('    .   ')
      })
    }
  }
}
</script>

